@media (width <= 575.98px) {
  #shopping-table th:first-child, #shopping-table td:first-child {
    display: none;
  }
}

#import-items-btn {
  margin-bottom: var(--pico-spacing);
}

.import-wrapper {
  display: inline-block;
  position: relative;
}

.import-wrapper .import-file-overlay {
  opacity: 0;
  cursor: pointer;
  z-index: 2;
  width: 100%;
  height: 100%;
  position: absolute;
  inset: 0;
}

.clickable-item {
  cursor: pointer;
  transition: color .15s, transform .15s, text-decoration-color .15s;
}

.clickable-item:hover {
  color: var(--pico-primary);
  transform: translateX(1px);
}

.clickable-item:active {
  transform: translateX(0);
}

.clickable-item:focus-visible {
  outline: 2px solid var(--pico-primary);
  outline-offset: 2px;
  border-radius: 4px;
}

.in-shopping-list {
  color: var(--pivot-success);
  font-weight: 600;
}

.struck .item-cell {
  color: var(--pivot-danger);
  text-decoration: line-through;
}

@media (width <= 575.98px) {
  #grocery-table th:nth-child(3), #grocery-table td:nth-child(3), #grocery-table th:nth-child(4), #grocery-table td:nth-child(4) {
    display: none;
  }
}

#import-button {
  margin-bottom: 0;
}

#import-file {
  clip: rect(0, 0, 0, 0);
  white-space: nowrap;
  border: 0;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}
